import React from 'react';
import { useCurrentWindow } from '../../src';

export function Clock() {
  const { close } = useCurrentWindow();
  return (
    <div>
      <div></div>
      <div style={{ display: 'flex', flexDirection: 'column', gap: 14 }}>
        <button onClick={close}></button>
        <button></button>
        <button></button>
      </div>
    </div>
  );
}
