import React from 'react';
import { WindowItem, WindowLauncher, launcher } from '../src';

import { Clock } from './apps/Clock';
import { AppDock } from './components/AppDock';
import { RouterDemo } from './apps/routerDemo';
import { AppContainer } from './components/AppContainer';

const Calendar = React.lazy(() => import('./apps/Calendar'));

const APP_LIST: WindowItem[] = [
  {
    title: '',
    key: 'calendar',
    size: {
      width: 600,
      height: 300,
      minWidth: 200,
      minHeight: 200,
    },
    component: (
      <AppContainer>
        <React.Suspense fallback={<div>...</div>}>
          <Calendar />
        </React.Suspense>
      </AppContainer>
    ),
  },
  {
    title: '',
    key: 'clock',
    component: (
      <AppContainer>
        <Clock />
      </AppContainer>
    ),
    size: {
      width: 200,
      height: 200,
    },
  },
  {
    title: '',
    key: 'router-demo',
    size: {
      width: 600,
      height: 400,
    },
    component: (
      <AppContainer>
        <React.Suspense fallback={<div>...</div>}>
          <RouterDemo />
        </React.Suspense>
      </AppContainer>
    ),
  },
];

export default function Root() {
  return (
    <div>
      <div style={{ display: 'flex', gap: 10, paddingBottom: 10 }}>
        {APP_LIST.map((item) => (
          <button
            key={item.key}
            onClick={() => {
              launcher.open(item.key);
            }}
          >
             {item.title}
          </button>
        ))}
      </div>

      {/*  */}
      <WindowLauncher windowList={APP_LIST} style={{ height: 700, background: '#eee' }} />

      {/*  */}
      <div>
        <AppDock />
      </div>
    </div>
  );
}
