import { useHistory } from '@/launcher';

export function PageB() {
  const history = useHistory();

  return (
    <div>
      <div>PageB</div>
      <div>
        <button
          onClick={() => {
            history.back();
          }}
        >
          
        </button>
        <button
          onClick={() => {
            history.go();
          }}
        >
          
        </button>
      </div>
    </div>
  );
}
